<template>
  <div class="e-piccheckbox">
    <div class="text">标题</div>
    <el-input v-model="data.templateTitle" size="small"></el-input>
    <div class="text">标题描述</div>
    <el-input v-model="data.templateDesc" size="small"></el-input>
    <div class="text">选项</div>
    <div
      class="piccheckbox"
      v-for="(item, index) in data.templateContent"
      :key="index"
    >
      <el-image
        class="image"
        :src="getPicUrl(index)"
        :preview-src-list="data.templateImgUrl.value"
      ></el-image>
      <el-input
        class="input"
        type="textarea"
        :autosize="areasize"
        size="large"
        resize="none"
        placeholder="请输入"
        v-model="data.templateContent[index].value"
      >
      </el-input>
      <el-button
        @click="delete_option(index)"
        type="danger"
        icon="el-icon-delete"
        circle
        size="mini"
        style="margin-left: 5px"
      >
      </el-button>
    </div>
    <el-upload
      class="upload-demo upload_div"
      drag
      action="http://jinshuju.max3.fgnwctvip.com/FormController/uploadImg"
      multiple
      ref="upload"
      :auto-upload="false"
      :on-change="getList"
      :file-list="fileList"
      :show-file-list="false"
      accept=".png,.jpeg,.jpg,.gif"
    >
      <i class="el-icon-upload"></i>
      <div class="el-upload__text">将文件拖到此处，或<em>点击上传</em></div>
      <div class="el-upload__tip" slot="tip">
        只能上传jpg/png文件，且不超过500kb
      </div>
    </el-upload>
    <el-switch
      active-text="是否必填"
      v-model="requireBoolean"
      active-color="#13ce66"
      inactive-color="#ff4949"
      @change="isRequire"
      style="margin:20px"
    >
    </el-switch>
  </div>
</template>

<script>
import comp from "@/utils/comp.js";
import { uploadImg, deleteImg, deleteOption } from "@/network/tabledata.js";
export default {
  data: function () {
    return {
      fileList: [],
      areasize: {
        minRows: 1,
        maxRows: 3,
      },
      requireBoolean: true
    };
  },

  methods: {
    getList(file, fileList) {
      let fileType = file.name.substr(file.name.lastIndexOf(".") + 1);
      if(fileType === "gif" || fileType === "jpg" || fileType === "jpeg" || fileType === "png"){
        this.fileList = fileList;
        let formData = new FormData();
        formData.append("file", file.raw);
        uploadImg(formData).then((res) => {
          let num = comp.getNumBycircluerSort(this.data.templateContent);
          //获取要删除图片的arr
          let picarr = this.$store.getters["tabledata/deletePicArr"];
          //添加标识，vuex新添图片对象是否存在
          let isHas = false;
          //遍历新增图片对象
          for (let r in picarr) {
            if (picarr[r].templateCount === this.data.templateCount) {
              //加入删除图片
              picarr[r].option.push(res.data.replace(/\\/g, "/"));
              isHas = true;
              break;
            }
          }
          //如果没有新增图片对象
          if (isHas === false) {
            picarr.push({
              templateCount: this.data.templateCount,
              option: [res.data.replace(/\\/g, "/")],
            });
          }
          this.data.templateContent.push({ key: num, value: String(file.uid) });
          this.data.templateImgUrl.push({
            key: num,
            value: this.$store.state.getURL + res.data.replace(/\\/g, "/"),
          });
        });
      }else{
        alert("文件格式不符")
      }
    },
    getPicUrl(index) {
      return this.data.templateImgUrl[index].value;
    },
    delete_option(index) {
      //如果该表不是新表
      if (
        this.data.formTemplateId !== null &&
        this.data.formTemplateId !== undefined &&
        this.data.formTemplateId !== ""
      ) {
        deleteOption(
          this.data.formTemplateId,
          this.data.templateContent[index]
        );
      }
      let imgUrl = this.data.templateImgUrl[index].value;
      // 分割字符串
      // console.log("delete---"+this.data.templateImgUrl[index])
      deleteImg(imgUrl.slice(imgUrl.lastIndexOf("img") - 1));
      this.data.templateContent.splice(index, 1);
      this.fileList.splice(index, 1);
      this.data.templateImgUrl.splice(index, 1);
    },
    isRequire() {
      if (this.requireBoolean === false) {
        this.data.templateRequire = 0;
      } else {
        this.data.templateRequire = 1;
      }
    }
  },
  computed: {
    data() {
      return this.$store.getters["tabledata/data"];
    },
    newOption() {
      return this.$store.getters["tabledata/newOption"];
    },
  },
  mounted: function() {
    if (this.$store.getters["tabledata/data"].templateRequire === 0) {
      this.requireBoolean = false;
    } else {
      this.requireBoolean = true;
    }
  }
};
</script>

<style scoped>
.e-piccheckbox >>> .text {
  text-align: left;
  font-size: 14px;
  margin: 20px 0 5px 0;
}
.e-piccheckbox >>> .el-input {
  margin: 5px 0;
}
.e-piccheckbox {
  padding: 0 15px;
}
.e-piccheckbox >>> div {
  display: block;
}
.e-piccheckbox >>> .el-upload-dragger {
  width: 100%;
  height: 80px;
}
.e-piccheckbox >>> .el-icon-upload {
  margin: 0 0 10px 0;
}
.e-piccheckbox >>> .image {
  width: 30px;
  height: 30px;
  margin-left: 10px;
  /* text-align: left; */
  position: relative;
}
.e-piccheckbox >>> .checkbox {
  margin-left: 10px;
  margin-right: 10px;
}
.e-piccheckbox >>> .piccheckbox {
  /* position:relative; */
  display: flex;
  text-align: left;
  border: 1px #dfdfdf solid;
  padding: 10px 0px;
  align-items: center;
}
.e-piccheckbox >>> .el-checkbox__label {
  display: none;
}
.e-piccheckbox >>> .input {
  width: 120px;
  margin-left: 10px;
  display: inline-block !important;
}
.e-piccheckbox >>> .el-image-viewer__actions__inner {
  display: flex !important;
}
.e-piccheckbox >>> .el-image-viewer__canvas {
  display: flex !important;
}
</style>