<template>
  <div class="e-checkbox">
    <div class="text">标题</div>
    <el-input v-model="data.templateTitle" size="small"></el-input>
    <div class="text">标题描述</div>
    <el-input v-model="data.templateDesc" size="small"></el-input>
    <el-switch
      active-text="是否必填"
      v-model="requireBoolean"
      active-color="#13ce66"
      inactive-color="#ff4949"
      @change="isRequire"
      style="margin:20px"
    >
    </el-switch>
  </div>
</template>
<script>
export default {
  data: function() {
    return {
      requireBoolean: true
    };
  },
  methods: {
    isRequire() {
      if (this.requireBoolean === false) {
        this.data.templateRequire = 0;
      } else {
        this.data.templateRequire = 1;
      }
    }
  },
  computed: {
    data() {
      return this.$store.getters["tabledata/data"];
    }
  },
  mounted: function() {
    if (this.$store.getters["tabledata/data"].templateRequire === 0) {
      this.requireBoolean = false;
    } else {
      this.requireBoolean = true;
    }
  }
};
</script>
<style scoped>
.e-checkbox >>> .text {
  text-align: left;
  font-size: 14px;
  margin: 20px 0 5px 0;
}
.e-checkbox >>> .el-input {
  margin: 5px 0;
}
.e-checkbox {
  padding: 0 15px;
}
.e-checkbox >>> div {
  display: block;
}
</style>
