<template>
<!-- 编辑页面左侧类别组件 -->
    <el-container>
        <el-header style="height:20px;font-size:14px;text-align:left;padding:0 10px">{{title}}</el-header>
        <el-main style="padding:0 10px" >
            <el-row :gutter="10" >
                <el-col :span="12" v-for="(item,index) in data1" :key="index">
                    <el-button @click.native="add(item)" :icon="item.icon+' iconfont'" size="medium" style="margin:5px 0px;width: 125px;text-align: left;">{{item.title}}</el-button>
                </el-col>
            </el-row>
        </el-main>
    </el-container>
</template>
<script>
import comp from '@/utils/comp.js'
export default {
    data:function(){
        return{
            data1:this.data
        }
    },
    props:{
        data:Array,
        title:String
    },
    methods:{
        add(data){
            //添加组件
            this.$store.commit('tabledata/increment',JSON.parse(JSON.stringify(comp.add_table_comp(data))))
        }
    }
}
</script>
<style>
    .iconfont{
        margin-right: 3px;
    }
</style>